"use client";

import { useState } from "react";
import { deleteUser } from "./actions";

type User = {
  id: number;
  name: string | null;
  email: string;
  createdAt: Date;
};

export default function UserDashboard({ initialUsers }: { initialUsers: User[] }) {
  const [query, setQuery] = useState("");

  // Client-side search logic
  const filteredUsers = initialUsers.filter((user) => {
    if (query.length > 0 && query.length < 3) return true;
    
    const searchStr = query.toLowerCase();
    return (
      user.name?.toLowerCase().includes(searchStr) ||
      user.email.toLowerCase().includes(searchStr)
    );
  });

  const isSearching = query.length >= 3;

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-10">
      {/* SEARCH COLUMN */}
      <section className="lg:col-span-1">
        <div className="sticky top-12 space-y-4">
          <div className="bg-zinc-900/50 p-6 rounded-2xl border border-zinc-800 shadow-xl">
            <label className="block text-xs font-bold text-zinc-500 uppercase tracking-widest mb-3">
              Search Directory
            </label>
            <div className="relative">
              <input
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                placeholder="Name or email..."
                className="w-full bg-zinc-950 p-3 pl-11 rounded-xl border border-zinc-800 focus:border-cyan-500 outline-none transition-all text-sm"
              />
              <div className="absolute left-4 top-3.5 text-zinc-500">
                <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><circle cx="11" cy="11" r="8"/><path d="m21 21-4.3-4.3"/></svg>
              </div>
            </div>

            <div className="mt-4 flex items-center justify-between min-h-[20px]">
              {isSearching ? (
                <p className="text-xs text-zinc-500 italic">
                  Searching for "<span className="text-cyan-400">{query}</span>"
                </p>
              ) : query.length > 0 ? (
                <p className="text-[10px] text-amber-500/70 uppercase font-bold tracking-tight">Need 3+ characters...</p>
              ) : (
                <p className="text-[10px] text-zinc-600 uppercase font-bold">Total: {initialUsers.length}</p>
              )}

              {query.length > 0 && (
                <button 
                  onClick={() => setQuery("")}
                  className="text-xs text-cyan-500 hover:text-cyan-400 font-bold cursor-pointer transition-colors"
                >
                  See All
                </button>
              )}
            </div>
          </div>
        </div>
      </section>

      {/* LIST COLUMN */}
      <section className="lg:col-span-2">
        <div className="grid gap-4">
          {filteredUsers.length === 0 ? (
            <div className="text-center p-12 border border-dashed border-zinc-800 rounded-3xl text-zinc-600 bg-zinc-900/10">
              No users found matching your search.
            </div>
          ) : (
            filteredUsers.map((user) => (
              <div 
                key={user.id} 
                className="group flex items-center justify-between p-5 rounded-2xl bg-zinc-900/30 border border-zinc-800 hover:bg-zinc-900/60 transition-all border-l-4 border-l-zinc-700 hover:border-l-cyan-500 shadow-sm"
              >
                <div>
                  <a href={`/user/${user.id}`} className="font-semibold text-zinc-100 hover:text-cyan-400 transition-colors">
                    {user.name || "Anonymous User"}
                  </a>
                  <p className="text-sm text-zinc-500">{user.email}</p>
                  <p className="text-[10px] text-zinc-700 uppercase mt-1 tracking-widest font-bold">
                    ID: {user.id} â€¢ Joined {new Date(user.createdAt).toLocaleDateString()}
                  </p>
                </div>

                <button
                  onClick={async () => {
                    if (confirm(`Delete ${user.name || user.email}?`)) {
                      await deleteUser(user.id);
                    }
                  }}
                  className="opacity-0 group-hover:opacity-100 p-2 text-zinc-500 hover:text-red-400 hover:bg-red-400/10 rounded-lg transition-all cursor-pointer"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M3 6h18"/><path d="M19 6v14c0 1-1 2-2 2H7c-1 0-2-1-2-2V6"/><path d="M8 6V4c0-1 1-2 2-2h4c1 0 2 1 2 2v2"/></svg>
                </button>
              </div>
            ))
          )}
        </div>
      </section>
    </div>
  );
}